ul.menu {
	margin: 0;
	padding: 0;
	display: flex;
	justify-content: space-between;
	z-index: 999;
	@media (max-width: 650px) {
		flex-direction: column;
	}
	@media (max-width: 590px) {
		flex-wrap: wrap;
	} 
	li {
		display: inline;
		margin-right: 10px;
		transition: .4s;
		a {
			padding: 10px;
			color: #000;
			text-shadow: 1px 1px 1px grey;
			font-size: 2rem;
			text-transform: uppercase;
			font-family: $font;
			letter-spacing: .2rem;
			position: relative;
			&:hover {
				color: #000;
			}
			&:after {
				position: absolute;
				height: 2px;
				width: 100%;
				background-color: #000;
				bottom: 0;
				left: 0;
				visibility: hidden;
				-webkit-transform: scaleX(0);
				transform: scaleX(0);
				transition: all .5s;
				content: '';
			}
			&:hover:after {
				visibility: visible;
				-webkit-transform: scaleX(1);
				transform: scaleX(1);
			}
			@media (max-width: 1100px) {
				font-size: 1.6rem;
			}
			@media(max-width: 800px) {
				font-size: 1.4rem;
			}
		}
	}
}

.hamMenu {
	text-align: center;
}